* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Barlow', 'Roboto', 'Montserrat', sans-serif;
}

body {
  max-width: 1400px;
  margin: 0 auto;
  background: #262626;
  background-position: top center;
  background-repeat: no-repeat;
  background-image: radial-gradient(50% 50% at top center,rgba(0,0,0,.66),#262626), linear-gradient(180deg,#fff,#262626) ;
  background-size: 120% 2000px,100% 2000px;
}
body::-webkit-scrollbar {
  width: 12px;
  height: 6px;
}
body::-webkit-scrollbar-thumb {
  background: #3F3A3C;
  border-radius: 10px;
}
body::-webkit-scrollbar-track {
  background: transparent;
}

a, a:hover, a:visited, a:active {
  color: inherit;
  text-decoration: none;
 }

/* NavigationBar */
.nav-container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding: 12px 30px;
  z-index: 100;
  background: rgba(0,0,0,.25);
}
.nav-logo a {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  font-size: 24px;
  font-weight:700;
  text-decoration: none;
  margin-right: 20px;
  padding: 0;
  border: none;
}
.contact-menu {
  display: flex;
  list-style-type: none;
}
.contact-menu a {
  font-family: 'Montserrat', sans-serif;
  color: #FFFFFF;
  font-size: 14px;
  font-weight:700;
  text-decoration: none;
  margin-left: 40px;
  padding: 0px;
  position: relative;
}
.contact-menu a:after {
  content: '';
  position: absolute;
  width: 80%;
  transform: scaleX(0);
  height: 3px;
  bottom: -4px;
  left: 0;
  background-color: #FFFFFF;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.contact-menu a:hover{
  cursor: pointer;
}
.contact-menu a:hover:after{
  transform: scaleX(1);
  transform-origin: bottom left;
}
.contact-menu .link-active a:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

/* Header */
.header-container {
  margin: 5px 30px;
  padding: 30px 10px;
  text-align: center;
  background: #FCF9EA;
  color: #3F3A3C;
  border-radius: 10px;
}
.header-container .img-container {
  width: 120px;
  height: 120px;
  margin: 0px auto;
  padding: 2px;
  outline: 4px solid #FFFEFF;
  border-radius: 50%;
  overflow: hidden;
  /* box-shadow:
  -5px -5px 15px rgba(255, 255, 255, 0.5),
  10px 10px 15px rgba(70, 70, 70, 0.25); */
}
.profile-img {
  object-fit: cover;
  object-position: -1.5px 12px;
  max-width: 90%;
  max-height: 90%;
  transform: scale(1.9) rotate(-1deg);
}
.header-container h1 {
  font-family: 'Montserrat', sans-serif;
  font-size: 32px;
  margin: 20px 0px 0px;
}
.header-container h2 {
  font-family: 'Barlow', sans-serif;
  font-size: 24px;
  font-weight: 400;
}
.header-container h3 {
  font-family: 'Barlow', sans-serif;
  font-size: 16px;
  font-weight: 400;
  padding: 5px 190px 0px;
}

/* Main */
.about-container {
  margin: 5px 30px;
  padding: 20px 200px;
  text-align: center;
  background: #FFFFFF; /* lighter */
  color: #3F3A3C;
  border-radius: 10px;
}
.about-container h2 {
  font-size: 30px;
  margin-bottom: 10px;
}
.about-container > p {
  /* font-size: 18px; */
  text-align: justify;
}
.about-container h3 {
  font-size: 24px;
  margin-top: 15px;
}
.about-container a {
  color: #3F3A3C;
  text-decoration: none;
  font-weight: bold;
  margin-top: 10px;
  font-family: 'Roboto', sans-serif;
  opacity: 1;
  text-decoration: underline;
}
.about-container button {
  color: #3F3A3C;
  text-decoration: none;
  font-weight: bold;
  margin-top: 10px;
  font-family: 'Roboto', sans-serif;
  opacity: 1;
  background: transparent;
  border: none;
  outline: none;
  padding: 0;
  margin: 0;
  font-size: 1em;
  text-decoration: underline;
}
.about-container a:hover {
  cursor: pointer;
  opacity: 0.9;
}
.about-container button:hover {
  cursor: pointer;
  opacity: 0.9;
}
.about-container button:active {
  border: none;
  outline: none;
}
.about-container button:focus {
  outline:0;
}

.skills-container {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0px 0px;
}
.skills-container li {
  margin: 20px 0px;
  width: 240px;
}
.skills-container .skill-img {
  height: 60px;
  margin: 0px 5px;
}
.skills-container h4 {
  font-size: 18px;
  margin: 5px 0px;
}

/* Footer */
.footer-container {
  display: block;
  text-align: center;
  padding: 30px 30px;
  z-index: 100;
  background: rgba(0,0,0,.5);
}
.footer-links {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}
.footer-links li {
  margin: 0px 20px 10px;
}
.footer-links a {
  font-size: 36px;
  color: #FFF7F7;
  opacity: .5;
}
.footer-links a:hover {
  opacity: 1;
}
.footer-container p {
  font-size: 16px;
  color: #FFF7F7;
  opacity: 0.8;
  margin-top: 10px;
}
.footer-container .disclaimer {
  font-size: 12px;
  color: #FFF7F7;
  opacity: 0.8;
  margin-top: 20px;
}

/* ContactModal */
.contact-modal {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.90);
  padding: 10px 60px 10px 60px;
  z-index: 200;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact-container {
  padding: 30px 120px;
  text-align: center;
  background: #FFFEF4;
  color: #3F3A3C;
  border-radius: 10px;
  position: relative;
  max-width: 100%;
}
.contact-links {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style-type: none;
}
.contact-links li {
  margin: 10px 20px;
}
.contact-links img {
  height: 36px;
}
.contact-links img:hover {
  transform: scale(1.25);
}
.contact-container p {
  font-size: 16px;
  margin: 10px 0px;
}
.contact-container button {
  font-size: 16px;
  position: absolute;
  top: 15px;
  right: 20px;
  background: none;
  border: none;
  opacity: 0.7;
}
.contact-container button:hover {
  cursor: pointer;
}

/* ProjectsView */
.project-container {
  display: flex;
  margin: 5px 30px;
  padding: 30px 100px;
  color: #3F3A3C;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
}
.project-container:nth-child(odd) {
  background: #FCF9EA;
}
.project-container:nth-child(even) {
  background: #FFFFFF;
}
.project-container:nth-child(even) .project-info {
  order: 2;
}
.project-container:nth-child(even) .img-container {
  order: 1;
}
.project-container:nth-child(even) .icon-container {
  order: 1;
}
.project-container h2 {
  margin-bottom: 10px;
  text-align: left;
}
.project-info {
  text-align: justify;
  width: 50%;
  padding: 0 20px;
}
.project-container .img-container {
  width: 50%;
  text-align: center;
  padding: 20px;

  position: relative;
}
.project-container .project-img {
  object-fit: contain;
  max-height: 320px;
  max-width: 100%;
  cursor: pointer;
  border-radius: 10px;
  box-shadow:
  -5px -5px 15px rgba(255, 255, 255, 0.5),
  10px 10px 15px rgba(70, 70, 70, 0.25);
  filter: brightness(0.95);
}
.project-container .project-img:hover {
  filter: brightness(0.85);
}
.project-container .fa-images {
  color: #FFFFFF;
  font-size: 20px;
  position: absolute;
  transform: scaleX(-1) translateX(100%) translateX(10px) translateY(10px);
  pointer-events: none;
}
.project-container .icon-container {
  width: 50%;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.project-container .skill-img {
  max-height: 80px;
  margin: 20px;
  cursor: auto;
  border-radius: 10px;
  flex-shrink: 1;
}
.tech-info {
  margin-top: 20px;
}
.tech-info ul {
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
}
.demo-links {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
}
.demo-links li {
  font-size: 12px;
  text-transform: uppercase;
  padding: 5px 0px;
  margin-right: 20px;
  border-radius: 10px;
  cursor: pointer;
  opacity: 0.9;
  color: #A6192E;
}
.demo-links li:hover {
  color: #3F3A3C;
  transform: scale(1.1);
}
.demo-links a {
  font-family: 'Montserrat',sans-serif;
  font-weight: bold;
  text-decoration: underline;
  margin-left: 5px;
}

/* ImgSwiper */
.swiper-container {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.90);
  padding: 10px 60px;
  z-index: 200;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.slides {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
  width: 70%;
  -ms-overflow-style: none;  /*  Hide scrollbar for IE and Edge */
  scrollbar-width: none;  /*  Hide scrollbar for Firefox */
}
.slides > div {
  scroll-snap-align: start;
  width: 100%;
  border-radius: 10px;
  background: white;
  background: transparent;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  padding: 0 1px;
}
.slides img {
  width: 100%;
  object-fit: contain;
  max-height: 100%;
}
.btn-slide {
  top: 50%;
  transform: translateY(-60%);
  font-size: 30px;
  background-color: transparent;
  border-bottom: none;
  position: absolute;
  cursor: pointer;
  border: none;
  opacity: .9;
  z-index: 10;
  color: #FFFEF4
}
.btn-slide-hidden {
  display: none;
}
.next {
  right: 40px;
}
.next > i {
  display: none;
}
.next:hover > i {
  display: block;
}
.next::before {
  content: '';
  position: absolute;
  width: 50vw;
  height: 100vh;
  right: -40px;
  top: 50%;
  transform: translate(0%, -50%);
  z-index:-1;
}
.prev {
  left: 40px;
}
.prev > i {
  display: none;
}
.prev:hover > i {
  display: block;
}
.prev::before {
  content: '';
  position: absolute;
  width: 50vw;
  height: 100vh;
  left: -40px;
  top: 50%;
  transform: translate(0%, -50%);
  z-index:-1;
}
.close {
  top: 30px;  
  right: 40px;
  z-index: 100;
}
.slides::-webkit-scrollbar {
  width: 10px;
  height: 6px;
  display: none; /* Hide scrollbar for Chrome, Safari and Opera */
}
.slides::-webkit-scrollbar-thumb {
  background: #A6192E;
  border-radius: 10px;
}
.slides::-webkit-scrollbar-track {
  background: transparent;
}
.dot-container ul {
  list-style-type: none;
  display: flex;
}
.slide-dot i {
  color: #A6192E;
  font-size: 5px;
  margin: 5px;
  transform: scale(2);
}
.slide-dot-inactive i {
  color: white;
  font-size: 5px;
  margin: 5px;
}


@media only screen and (max-width: 1200px) {
  /* Header */
  .header-container h3 {
    padding: 5px 20px 0px;
  }

  /* Main */
  .about-container {
    padding: 20px 160px;
  }
  .skills-container li {
    width: 220px;
    width: 33%;
  }

  /* ContactModal */
  .contact-modal {
    padding: 10px 15px;
  }

  /* ProjectsView */
  .project-container {
    padding: 30px 80px;
  }
}


@media only screen and (max-width: 992px) {
  /* Main */
  .about-container {
    padding: 20px 120px;
  }
  .skills-container li {
    width: 200px;
  }

  /* ProjectsView */
  .project-container {
    flex-wrap: wrap;
    padding: 30px 30px;
  }
  .project-info {
    padding: 0 10px;
  }
  .project-container .img-container {
    padding: 10px;
  }
  .project-container .project-img {
    object-fit: contain;
    max-height: 380px;
    width: 100%;
  }
  .project-container .icon-container {
    padding: 0px 15px;
    flex-direction: column;
  }
  .project-container .skill-img {
    margin: 10px;
  }

  /* ImgSwiper */
  .swiper-container {
    padding: 15px;
  }
  .slides {
    width: 85%;
  }
}


@media only screen and (max-width: 768px) {
  /* Header */
  .about-container {
    padding: 20px 30px;
  }
  .skills-container li {
    width: 220px;
  }

  /* ProjectsView */
  .project-container .project-img {
    object-fit: contain;
    width: 90%;
  }
  .project-container:nth-child(even) .project-info {
    order: 1;
  }
  .project-container:nth-child(even) .img-container {
    order: 2;
  }
  .project-container:nth-child(even) .icon-container {
    order: 2;
  }
  .project-info {
    width: 100%;
  }
  .project-container .img-container {
    width: 100%;
    margin-bottom: 0px;
  }
  .project-container .icon-container {
    width: 100%;
    padding: 0px 15px;
    flex-direction: row;
  }

  /* ImgSwiper */
  .slides {
    width: 85%;
  }
}


@media only screen and (max-width: 576px) {
  /* NavigationBar */
  .nav-container {
    padding: 12px 15px;
    justify-content: center;
  }
  .nav-logo a {
    margin: 0px;
  }
  .contact-menu {
    width: 100%;
    justify-content: center;
    margin-top: 6px;
  }
  .contact-menu a {
    margin: 0px;
  }
  .contact-menu li {
    flex: 1;
    display: flex;
    justify-content: center;
  }

  /* Header */
  .header-container {
    margin: 5px 15px;
    padding: 30px 0px;
  }
  .header-container h1 {
    font-size: 28px;
    padding: 0px 20px;
  }
  .header-container h2 {
    padding: 0px 20px;
  }
  .header-container h3 {
    font-size: 16px;
    padding: 5px 20px 0px;
  }

  /* Main */
  .about-container {
    margin: 5px 15px;
    padding: 20px 20px;
  }
  .about-container > p {
    font-size: initial;
    line-height: initial;
  }
  .skills-container li {
    width: 200px;
  }

  /* Footer */
  .footer-container {
    padding: 30px 15px;
  }

  /* ContactModal */
  .contact-container {
    padding: 20px;
  }

  /* ProjectsView */
  .project-container {
    margin: 5px 15px;
    padding: 20px 0px;
  }
  .project-container h2 {
    font-size: 22px;
  }
  .project-container .img-container {
    width: 100%;
    margin-bottom: 0px;
    padding: 20px 30px;
  }
  .project-container .project-img {
    object-fit: contain;
    width: 100%;
  }
  .project-container .skill-img {
    max-height: 60px;
    margin: 10px;
  }
    
  /* ImgSwiper */
  .slides {
    width: 100%;
  }
  .next {
    right: 20px;
    color: #3F3A3C;
    opacity: .3;
  }
  .prev {
    left: 20px;
    color: #3F3A3C;
    opacity: .3;
  }
  .close {
    top: 30px;  
    right: 15px;
  }
}